﻿.am-slider-default {
    margin: 0 !important;
}

    .am-slider-default .am-control-nav {
        bottom: 15px !important;
    }

.am-tabs-bd {
    border: 0;
}

.am-nav-tabs {
    border: 0;
    margin: 0.5rem 0 1.2rem 0;
}

    .am-nav-tabs > li.am-active > a {
        border: 0;
        background: #24ae44 !important;
        color: #fff !important;
    }

    .am-nav-tabs > li > a {
        color: #7f7f7f;
        border: 1px solid #e1e1e1;
        outline: none;
    }

        .am-nav-tabs > li > a:hover {
            border-color: #e1e1e1;
        }

.am-tabs-bd .am-tab-panel {
    padding: 0;
}

.index-honor .am-control-nav, .index-product .am-control-nav {
    display: none;
}

.index-honor .am-slider-default, .index-product .am-slider-default {
    box-shadow: none;
}

.index-search {
    width: 100%;
    background: #d6d6d6;
    overflow: hidden;
    float: left;
    padding: 1rem 0;

    &-g {
        max-width: 1100px;
        margin: auto;

        & > div {
            span {
                color: #ff5400;
            }

            a {
                color: #5a5a5a;
                font-style: normal;
                margin-right: 1rem;

                &:hover {
                    color: @hover-text;
                }
            }

            input {
                border-radius: 20px;
                border: 1px solid #959595;
                padding: .5rem 0 .5rem 1.5rem;
                font-size: 1.5rem;
                margin-right: 0.8rem;
                width: 24rem;
                padding-right: 1rem;
                outline: none;

                &:focus {
                    border: 1px solid #b5b5b5 !important;
                    -moz-box-shadow: inset 2px 1px 5px #c9c8c8;
                    -webkit-box-shadow: inset 2px 1px 5px #c9c8c8;
                    -o-box-shadow: inset 2px 1px 5px #c9c8c8;
                    box-shadow: inset 2px 1px 5px #c9c8c8;
                }
            }

            button {
                background: @on-color;
                color: #fff;
                border: 0;
                border-radius: 5px;
                padding: .5rem 1.2rem;
                font-size: 1.4rem;

                i {
                    padding-right: 0.3rem;
                    float: left;
                    margin-top: 0.1rem;
                }

                &:hover {
                    background: @hover-color;
                }
            }
        }
    }
}

.index-new {
    width: 100%;
    float: left;
    overflow: hidden;
    margin-top: 2rem;

    .cml-g > div {
        padding: 0px;
    }

    &-left {
        margin-right: 1rem;

        h1 {
            color: #7f7f7f;
            font-size: 1.6rem;
        }
    }

    &-right {
        margin-left: 1rem;
    }

    &-about {
        border-radius: 3px;
        border: 1px solid #d8d8d8;
        padding: 1rem;
        margin-top: 1rem;


        h1 {
            color: #7f7f7f;
            font-size: 1.6rem;
            font-weight: 500;
            overflow: hidden;
        }

        ul {
            overflow: hidden;
        }

        li {
            width: 25%;
            float: left;

            em {
                width: 4rem;
                height: 4rem;
                border: 1px solid #b6b6b6;
                border-radius: 3px;
                margin: auto;
                display: block;
                text-align: center;
                line-height: 4rem;
                box-shadow: 0px 1px 3px 1px rgba(0, 0, 0,0.1);

                &:hover {
                    background: #c9c8c8;

                    & i {
                        color: #fff;
                    }
                }

                & i {
                    color: #17b760;
                    font-size: 2.8rem;
                }
            }

            span {
                display: block;
                text-align: center;
                color: #7f7f7f;
                font-size: 1.4rem;
                padding-top: 0.5rem;
            }
        }
    }

    &-tab {
        border-radius: 3px;
        border: 1px solid #d8d8d8;
        padding: 1rem;
        margin-top: 1rem;


        &-img {
            float: left;

            img {
                width: 100%;
                margin-top: 0.5rem;
            }
        }

        &-main {
            & > div {
                padding: 0;
                margin: 0;
            }
        }

        &-list {
            padding-left: 1rem;


            li {
                padding: 0.3rem 0;
                overflow: hidden;
            }

            strong {
                font-weight: 500;
                display: block;
            }

            span {
                display: block;
                text-align: right;
            }


            & div {
                padding: 0px;
            }
        }
    }
}


.index-honor {
    width: 100%;
    float: left;
    margin-top: 2rem;

    &-silder {
        margin-top: 2rem;

        li {
            padding: 0 0.5rem;
        }

        img {
        }

        span {
            display: block;
            width: 100%;
            margin-top: 0.5rem;
            color: #7f7f7f;
            font-size: 1.5rem;
        }
    }

    & li {
        padding: 0 0.5rem;
    }
}

.index-product {
    width: 100%;
    float: left;
    margin-top: 2rem;

    li {
        padding: 0 1rem;
    }

    span {
        text-align: center;
        display: block;
        margin-top: 0.5rem;
        color: #7f7f7f;
        font-size: 1.5rem;
    }

    img {
        border: 1px solid #c2c2c2;
        border-radius: 3px;
        border: 2px solid #c2c2c2;
        padding: 1rem;
    }

    &-silder {
        margin-top: 2rem;
    }
}
